import React, { useState, useEffect } from 'react'; // 导入 useState 和 useEffect
import { useNavigate } from 'react-router-dom';
import QrScanner from 'react-qr-scanner';
import './Game1.scss';

const Game = () => {
  const [isQRCodeScanned, setIsQRCodeScanned] = useState(false);
  const [scannedMessage, setScannedMessage] = useState('');
  const [hasPermission, setHasPermission] = useState(false);
  const navigate = useNavigate();

  // 处理扫描到的数据
  const handleScan = (data) => {
    if (data) {
      const text = data.text || '';
      if (text.includes('游戏结束')) {
        setIsQRCodeScanned(true);
        setScannedMessage(text);
      }
    }
  };

  // 处理扫描错误
  const handleError = (err) => {
    console.error(err);
  };

  // 请求媒体设备访问权限
  const requestCameraPermission = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ video: true });
      setHasPermission(true);
    } catch (err) {
      console.error('Error accessing camera:', err);
    }
  };

  useEffect(() => {
    requestCameraPermission();
    return () => {
      // 组件卸载时的清理操作
    };
  }, []);

  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 5 } });
  };

  const previewStyle = {
    height: '100%',
    width: '100%',
    objectFit: 'cover',
  };

  return (
    <div className="container">
      {!isQRCodeScanned ? (
        <div className="scannerContainer">
          {!hasPermission ? (
            <p className="text">请授予摄像头权限以继续</p>
          ) : (
            <>
              <QrScanner
                delay={300}
                style={previewStyle}
                onError={handleError}
                onScan={handleScan}
              />
              <p className="text">请扫描二维码以继续</p>
            </>
          )}
        </div>
      ) : (
        <div className="resultContainer">
          <p className="text">{scannedMessage}</p>
          <button className="button" onClick={handleEndGame}>游戏结束</button>
        </div>
      )}
    </div>
  );
};

export default Game;



