//介面會有一小小樹蛙神一隻在神社中心位置。

import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Game = () => {
    const navigate = useNavigate();

    const handleEndGame = () => {
        navigate('/', { state: { dialogIndex: 47 } });
    };

    useEffect(() => {
        const handleMessage = (event) => {
            // 确认消息来源是预期的 iframe
            if (event.origin !== 'https://sd918207458.github.io') {
                return;
            }

            // 处理来自 iframe 的消息
            if (event.data.type === 'END_GAME') {
                handleEndGame();
            }
        };

        window.addEventListener('message', handleMessage);

        // 清除事件监听器
        return () => {
            window.removeEventListener('message', handleMessage);
        };
    }, []);

    return (
        <div>
            <button onClick={handleEndGame}>遊戲結束</button>
            <iframe
                src="https://sd918207458.github.io/AR模板/index.html"
                style={{ width: '100vw', height: '100vh', border: 'none' }}
                title="AR Scene"
            ></iframe>
        </div>
    );
};

export default Game;
