import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import assemblyImage from '../../assets/志工阿姨1-常駐表情.png'; // 引入組裝相機的圖片
import './Game2.scss';
import background from '../../assets/picture/BG/背景-活動中心.png'; // 引入背景圖片

const Game = () => {
  const [isAssembling, setIsAssembling] = useState(false); // 狀態：是否顯示組裝說明圖
  const navigate = useNavigate();

  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 9 } });
  };

  const handleStartAssembling = () => {
    setIsAssembling(true); // 設置為顯示組裝說明圖
  };

  return (
    <div
      className="container"
      style={{
        backgroundImage: `url(${background})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        height: '100vh',
        width: '100vw',
      }}
    >
      {!isAssembling ? (
        <div className="content">
          <img src={assemblyImage} alt="組裝相機說明圖" className="image" />
          <button className="button" onClick={handleStartAssembling}>開始修相機</button>
        </div>
      ) : (
        <div className="content">
          <img src={assemblyImage} alt="組裝相機說明圖" className="image" />
          <button className="button" onClick={handleEndGame}>修好了</button>
        </div>
      )}
    </div>
  );
};

export default Game;
