import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import QrReader from 'react-qr-scanner';
import './Game5.scss'; // 導入 SCSS 文件

const Game = () => {
  const [scannedPhoto, setScannedPhoto] = useState(null);
  const navigate = useNavigate();

  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 41 } });
  };

  const handleScan = (data) => {
    if (data) {
      setScannedPhoto(data); // 模擬獲得照片
    }
  };

  const handleError = (err) => {
    console.error(err);
  };

  return (
    <div className="game-container">
      <button onClick={handleEndGame}>遊戲結束</button>
      <div className="scanner-container">
        <div className="qr-reader">
          <QrReader
            delay={300}
            onError={handleError}
            onScan={handleScan}
            style={{ width: '100%' }}
          />
          <div className="align-box"></div>
        </div>
        {scannedPhoto && (
          <div className="photo-container">
            <p>掃描成功！獲得照片：</p>
            <img src={scannedPhoto} alt="Scanned QR" />
          </div>
        )}
      </div>
    </div>
  );
};

export default Game;
