import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import mapImage from '../../assets/志工阿姨1-常駐表情.png'; // 引入地圖背景圖片，確保路徑正確
import audio1 from '../../assets/alert.mp3'; // 引入音效文件1
import audio2 from '../../assets/alert.mp3'; // 引入音效文件2
import audio3 from '../../assets/alert.mp3'; // 引入音效文件3
import './Game4.scss'; // 導入 SCSS 文件

const Game = () => {
  const [audioPlayed, setAudioPlayed] = useState([false, false, false]); // 音頻是否已播放
  const [inputValue, setInputValue] = useState(''); // 輸入框的值
  const [showEndButton, setShowEndButton] = useState(false); // 控制顯示遊戲結束按鈕
  const navigate = useNavigate();

  // 播放錄音
  const handlePlayAudio = (index) => {
    const audioFiles = [audio1, audio2, audio3]; // 音頻文件數組
    const audio = new Audio(audioFiles[index]);
    audio.play();
    setAudioPlayed(audioPlayed.map((played, i) => (i === index ? true : played)));

    // 如果點擊的是第二個音頻，檢查輸入框值並顯示遊戲結束按鈕
    if (index === 1) {
      checkInputValue();
    }
  };

  // 檢查輸入框的值
  const checkInputValue = () => {
    if (inputValue === '手水舍') {
      setShowEndButton(true); // 顯示遊戲結束按鈕
    }
  };

  // 結束遊戲
  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 38 } });
  };

  // 清除結果和已播放的音頻
  const clearResultAndAudio = () => {
    setInputValue(''); // 清空輸入框
    setAudioPlayed([false, false, false]);
    setShowEndButton(false); // 隱藏遊戲結束按鈕
  };

  return (
    <div className="container">
      <div className="mapContainer">
        {/* 地圖背景圖片 */}
        <img src={mapImage} alt="地圖圖片" className="mapImage" />
      </div>
      <div className="buttonsContainer">
        {/* 按鈕組 */}
        <button
          className={`button ${audioPlayed[0] ? 'audioPlayed' : ''}`}
          onClick={() => handlePlayAudio(0)}
          disabled={audioPlayed[0]}
        >
          播放錄音 1
        </button>
        <button
          className={`button ${audioPlayed[1] ? 'audioPlayed' : ''}`}
          onClick={() => handlePlayAudio(1)}
          disabled={audioPlayed[1]}
        >
          播放錄音 2
        </button>
        <button
          className={`button ${audioPlayed[2] ? 'audioPlayed' : ''}`}
          onClick={() => handlePlayAudio(2)}
          disabled={audioPlayed[2]}
        >
          播放錄音 3
        </button>
      </div>
      <div className="resultContainer">
        {/* 結果顯示區域 */}
        <input
          type="text"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          onBlur={checkInputValue} // 當輸入框失去焦點時檢查值
          placeholder="請輸入關鍵字"
          className="input"
        />
        {showEndButton && (
          <div>
            {/* 結果為特定值時顯示的內容 */}
            <button className="button" onClick={handleEndGame}>
              遊戲結束
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default Game;
